@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.request-audit__content {
  padding: 13rem 50px 16.9rem;
  background: url("../img/fon.png") center/cover no-repeat;
  overflow: hidden;
  overflow: hidden;
  border-radius: 30px;
}
@media screen and (max-width: 960px) {
  .request-audit__content {
    padding: 13rem 30px 16.9rem;
  }
}
@media screen and (max-width: 690px) {
  .request-audit__content {
    border-radius: 15px;
    padding: 50px 30px;
  }
}
@media screen and (max-width: 490px) {
  .request-audit__content {
    padding: 50px 10px;
  }
}
.request-audit__body {
  display: grid;
  grid-template-columns: 1fr;
  position: relative;
}
.request-audit__row {
  position: relative;
  z-index: 9;
}
.request-audit__header {
  margin: 0 auto;
}
.request-audit__title {
  margin: 0 auto 40px;
  max-width: 1175px;
  font-size: 76px;
  font-weight: 400;
  line-height: 100%;
  text-align: center;
}
@media screen and (max-width: 490px) {
  .request-audit__title {
    margin-bottom: 20px;
  }
}
.request-audit__text {
  text-align: center;
  font-size: 20px;
  font-weight: 400;
  line-height: 125%; /* 25px/20 */
  max-width: 720px;
  margin: 0 auto;
}
.request-audit__form {
  display: flex;
  justify-content: space-between;
  gap: 25px;
  margin: 40px auto;
  max-width: 892px;
  display: flex;
}
.request-audit__form .order-form__button {
  width: 100%;
}
@media screen and (max-width: 960px) {
  .request-audit__form {
    flex-direction: column;
  }
}
@media screen and (max-width: 490px) {
  .request-audit__form {
    gap: 12px;
    margin: 16px auto;
  }
}
.request-audit__footer {
  margin: 0 auto;
  max-width: 780px;
}
.request-audit__footer .request-audit__text {
  margin-bottom: 17px;
}
.request-audit__phone {
  font-size: 40px;
  font-weight: 400;
  line-height: 100%;
  text-align: center;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .request-audit__phone {
    font-size: calc(20px + 20 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .request-audit__phone {
    font-size: 20px;
  }
}

.audit-form__column {
  box-sizing: border-box;
  border-radius: 30px;
  overflow: hidden;
  align-self: center;
}
@media screen and (max-width: 960px) {
  .audit-form__column {
    max-width: 260px;
    width: 100%;
  }
}
@media screen and (max-width: 490px) {
  .audit-form__column {
    max-width: 195px;
    width: 100%;
  }
}
.audit-form__column:not(:last-child) {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  flex: 1 1 auto;
}
.audit-form__column:last-child {
  align-self: center;
}
@media screen and (max-width: 960px) {
  .audit-form__column:last-child {
    width: 268px;
  }
}
@media screen and (max-width: 375px) {
  .audit-form__column:last-child {
    width: 100%;
  }
}
.audit-form__line {
  height: 100%;
  width: 100%;
}
.audit-form__input {
  display: flex;
  align-items: center;
  height: 5.8rem;
}
.audit-form__input input {
  padding-left: 10px;
  display: flex;
  align-items: center;
  color: #2c2c2c;
  width: 100%;
  height: 100%;
  line-height: 156.25%; /* 25px/16 */
  font-size: 16px;
}
@media screen and (max-width: 490px) {
  .audit-form__input input {
    padding: 7px 2rem 10px;
  }
}
@media screen and (max-width: 690px) {
  .audit-form__input {
    height: 36px;
  }
}
.audit-form__button {
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
.audit-form__button:hover {
  border: 1px solid #fff;
}
.audit-form__button:hover .icon {
  transform: rotate(-45deg);
}
.audit-form__button:hover span {
  color: #fff;
}
.audit-form__button:hover::before {
  background: #2b3769;
}
.audit-form__button .icon-arrow-right {
  color: #fff;
  font-size: 16px;
}
.audit-form__button .icon-arrow-right::before {
  font-weight: 500;
}